  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: url("fonts/Montserrat-Light.woff") format('woff');
  font-weight: 300;
  font-style: normal;
}
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
em,
img,
b,
u,
i,
ol,
ul,
li,
form,
label,
table,
tbody,
tfoot,
thead,
tr,
th,
td,
input,
textarea,
button {
  margin: 0;
  padding: 0;
  border: none medium;
}
html,
body {
  font-family: 'Montserrat', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background: #fff;
  color: $baseFont;
}
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
img {
  vertical-align: bottom;
  max-width: 100%;
}
@media screen and (min-width: 768px) {
  ::-webkit-scrollbar {
    width: 10px;
    height: 5px;
  }
}
@media screen and (min-width: 768px) {
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #eee;
  }
}
@media screen and (min-width: 768px) {
  ::-webkit-scrollbar-thumb {
    background-color: #d6d6d6;
  }
}
*,
*:focus,
*:after,
*:before {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
}
input,
textarea {
  font-family: 'Montserrat', Arial, sans-serif;
  font-size: 15px;
  width: 100%;
  background: none;
  border: none medium;
  resize: none;
  box-shadow: none;
}
input::placeholder,
textarea::placeholder {
  font-family: 'Montserrat', Arial, sans-serif;
  text-overflow: ellipsis;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #fff inset !important;
}
article,
aside,
footer,
header,
nav,
section,
canvas {
  display: block;
}
article ol,
aside ol,
footer ol,
header ol,
nav ol,
section ol,
canvas ol,
article ul,
aside ul,
footer ul,
header ul,
nav ul,
section ul,
canvas ul {
  list-style: none;
}
a,
button,
input[type="submit"],
input[type="button"],
input[type="reset"],
label {
  border: none medium;
  outline: none;
  cursor: pointer;
  background: none;
  -webkit-text-decoration-skip: objects;
}
a,
a:active {
  text-decoration: none;
  color: #0a52bf;
  transition: 0.3s;
}
a:visited,
a:active:visited {
  color: #04337b;
}
a:hover,
a:active:hover {
  color: #418dff;
}
h1,
h2,
h3,
h4,
b,
strong {
  font-family: 'Montserrat';
  font-weight: normal;
  color: #000;
  padding: 0;
  margin: 0;
}
h1 {
  font-size: 34px;
  font-weight: 600;
  line-height: 1.3em;
}
@media screen and (max-width: 767px) {
