.b-back-link {
  position: relative;
  width: 60px;
  height: 60px;
  background: #147115;
  display: block;
  transition: 0.4s;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .b-back-link {
    width: 50px;
    height: 50px;
  }
}
@media screen and (max-width: 575px) {
  .b-back-link {
    width: 40px;
    height: 40px;
  }
}
@media screen and (max-width: 1023px) {
  .b-back-link {
    display: flex;
  }
  .b-back-link:before {
    content: 'Назад в раздел';
    width: 250px;
    position: absolute;
    display: block;
    align-items: center;
    left: 100%;
    padding-left: 10px;
    color: #000;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
  }
}
.b-back-link:after {
  content: '';
  display: block;
  position: absolute;
  width: 40%;
  height: 40%;
  left: 60%;
  top: 50%;
  border-bottom: solid 2px #fff;
  border-left: solid 2px #fff;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: 0.4s;
}
.b-back-link:hover {
  background: #1da11e;
}
.b-back-link:active {
  background: #19bc1b;
}
@media screen and (max-width: 1023px) and (max-width: 575px) {
  .b-back-link:before {
    font-size: 15px;
  }
}
