  color: #0a52bf;
}
.b-content-file__info a:hover {
  color: #418dff;
}
.b-content-slider__slide + .b-content-slider__slide {
  margin-left: 20px;
}
.b-breadcrumbs {
  margin-top: 45px;
}
@media screen and (max-width: 1023px) {
  .b-breadcrumbs {
    margin-bottom: 20px;
    margin-top: 20px;
  }
}
@media screen and (max-width: 390px) {
  .b-breadcrumbs {
    margin: 10px 0;
  }
}
.b-breadcrumbs__wrapper {
  padding: 11px 0;
  display: flex;
  flex-wrap: wrap;
  max-width: 76%;
}
.b-breadcrumbs__item {
  position: relative;
  font-size: 14px;
  line-height: 1.4em;
  color: #a0aad4;
  padding-right: 12px;
  margin-right: 10px;
}
.b-breadcrumbs__item:after {
  content: '';
  position: absolute;
  right: -5px;
  top: 10px;
  width: 10px;
  height: 1px;
  font-size: 10px;
  line-height: 12px;
  background: #a0aad4;
  transform: rotate(-50deg);
}
.b-breadcrumbs__item:last-child {
  padding-right: 0;
  margin-right: 0;
}
.b-breadcrumbs__item:last-child:after {
  display: none;
}
.b-breadcrumbs__item a {
  color: #111b47;
  transition: 0.1s;
}
.b-breadcrumbs__item:hover a {
  color: #e70b00;
}
.b-network {
  display: flex;
}
.b-network__item {
  width: 60px;
  height: 60px;
  border: solid 1px #9a9a9a;
  position: relative;
  display: block;
  transition: 0.5s;
  background: #fff;
}
@media screen and (max-width: 767px) {
  .b-network__item {
    width: 50px;
    height: 50px;
  }
}
@media screen and (max-width: 575px) {
  .b-network__item {
    width: 40px;
    height: 40px;
  }
}
.b-network__item img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: block;
  transition: 0.5s;
}
@media screen and (max-width: 767px) {
  .b-network__item img {
    max-width: 80%;
  }
}
@media screen and (max-width: 575px) {
  .b-network__item img {
    max-width: 50%;
  }
}
.b-network__item:hover {
  background: #e9e9e9;
}
@media screen and (max-width: 1023px) {
  .b-network__right {
    display: flex;
  }
}
@media screen and (max-width: 1023px) {
  .b-network__right .b-network__item:nth-child(1) {
    border-bottom: solid 1px #9a9a9a;
    border-right: 0;
  }
}
.b-network__right .b-network__item:nth-child(2) {
  border-bottom: 0;
  border-top: 0;
}
@media screen and (max-width: 1023px) {
  .b-network__right .b-network__item:nth-child(2) {
    border-bottom: solid 1px #9a9a9a;
    border-top: solid 1px #9a9a9a;
    border-right: 0;
  }
}
.b-network__left .b-network__item {
  border-right: 0;
}
.b-tab {
  border-bottom: solid 1px #cac7c7;
}
.b-tab__content {
  display: none;
  padding-top: 10px;
}
.b-tab__content-inner {
  display: flex;
}
.b-tab__content-ico {
  flex: 0 0 30px;
  max-width: 30px;
  margin: 0 30px 0 0;
}
@media screen and (max-width: 991px) {
  .b-tab__content-ico {
    display: none;
  }
}
.b-tab__content-text {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4em;
  color: #000;
}
.b-tab__content-text p {
  padding: 0 0 20px 0;
}
.b-tab__name {
  cursor: pointer;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.1em;
  color: #000;
  padding: 10px 10% 10px 0;
  margin: 30px 0 0;
  position: relative;
}
.b-tab__name:after {
  content: '';
  position: absolute;
  top: 10px;
  right: 10px;
  width: 15px;
  height: 15px;
  display: block;
  border-left: solid 2px #147115;
  border-bottom: solid 2px #147115;
  transform: rotate(-45deg);
  transition: 0.3s;
}
.b-tab__name.__active:after {
  content: '';
  border-left: solid 2px #147115;
  border-bottom: solid 2px #147115;
  transform: rotate(135deg);
}
.b-pagination {
  margin-top: 60px;
}
.b-pagination__list {
  display: flex;
}
.b-pagination__item._active {
  color: #fff;
  background: #000;
}
.b-pagination__item._active a {
  color: #fff;
}
.b-pagination__item._active a:hover {
  cursor: default;
  color: #fff;
}
.b-pagination__item a {
  display: block;
  transition: 0.4s;
  padding: 3px 11px;
  border: solid 1px transparent;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.5em;
}
.b-pagination__item a:hover {
  border-color: #000;
  color: #000;
}
.b-popup {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.5);
  display: none;
  z-index: 999;
  overflow: hidden;
}
.b-popup._active {
  display: block;
}
.b-popup__wrap {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  padding: 70px 100px;
  display: block;
  z-index: 9999;
  border-radius: 16px;
  box-shadow: 0px 4px 55px rgba(0,0,0,0.25);
}
@media screen and (max-width: 991px) {
  .b-popup__wrap {
    width: 80%;
  }
}
@media screen and (max-width: 767px) {
  .b-popup__wrap {
    padding: 70px 20px;
  }
}
@media screen and (max-width: 575px) {
  .b-popup__wrap {
    width: 90%;
  }
}
.b-popup__text {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.3em;
  text-align: center;
  color: #000;
}
.b-popup__inner {
  text-align: center;
}
.b-popup__img {
  padding-bottom: 30px;
}
.b-popup__close {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.b-popup__close:after,
.b-popup__close:before {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background: #000;
  position: absolute;
  top: 50%;
  left: 0;
}
.b-popup__close:after {
  transform: rotate(45deg);
}
.b-popup__close:before {
  transform: rotate(-45deg);
}
.b-vs__overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(45,49,67,0.6);
  display: none;
  z-index: 500;
}
.b-vs__overlay._active {
  display: block;
}
.b-vs__wrap {
  position: fixed;
  width: 71%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 600;
  overflow: hidden;
  display: none;
}
@media screen and (max-width: 991px) {
  .b-vs__wrap {
    width: 100%;
  }
}
.b-vs__wrap._active {
  display: block;
}
.b-vs__list {
  position: relative;
}
.b-vs__list .owl-stage {
  display: flex;
}
@media screen and (max-width: 991px) {
  .b-vs__list .owl-nav {
    display: none;
  }
}
.b-vs__list .owl-nav button {
  position: absolute;
  top: 50%;
  text-indent: -99999px;
  position: absolute;
  background-image: url("/f/img/arrow-slider.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  width: 25px;
  height: 55px;
}
.b-vs__list .owl-item {
  position: relative;
  display: flex;
  align-items: center;
}
.b-vs__list .owl-prev {
  left: 0;
  position: absolute;
  transform: translateY(-50%);
}
.b-vs__list .owl-next {
  right: 0;
  position: absolute;
  transform: translateY(-50%) rotate(180deg);
}
.b-vs__item {
  width: 100%;
}
.b-vs__item-inner {
  text-align: center;
  background: #fff;
  display: block;
  padding: 40px 30px;
  width: 90%;
  margin: 0 auto;
  border-radius: 16px;
}
@media screen and (max-width: 991px) {
  .b-vs__item-inner {
    width: 98%;
  }
}
@media screen and (max-width: 767px) {
  .b-vs__item-inner {
    padding: 20px 15px;
  }
}
.b-vs__iframe {
  margin-bottom: 20px;
  position: relative;
  padding-top: 50%;
}
@media screen and (max-width: 767px) {
  .b-vs__iframe {
    padding-top: 60%;
  }
}
@media screen and (max-width: 575px) {
  .b-vs__iframe {
    padding-top: 70%;
  }
}
.b-vs__iframe iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.b-vs-text {
  text-align: left;
}
